/**********************************
GENERAL
***********************************/
@font-face 
{
  font-family: 'NeutraText';
  src: url("/shared/NeutraText-DemiItalic.woff") format("woff2"), url("/shared/NeutraText-DemiItalic.woff") format("woff");
  font-weight: bold;
  font-style: italic;
}


@font-face 
{
  font-family: 'NeutraText';
  src: url("/shared/NeutraText-Book.woff") format("woff2"), url("/shared/NeutraText-Book.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}


@font-face 
{
  font-family: 'NeutraText';
  src: url("/shared/NeutraText-Demi.woff") format("woff2"), url("/shared/NeutraText-Demi.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}


body 
{
  font-family: NeutraText;
  font-style: normal;
  font-size: 1em;
  line-height: 1em;
  background-color: #fff;
  color: #000;
}

#wrapper 
{
  max-width: 940px;
  margin: 0 auto;
  padding: 0 5%;
}

img 
{
  max-width: 100%;
}

a 
{
  text-decoration: none;
  color: #000;
  &:link, &:visited 
  {
    color: #000;
  }
  &:hover 
  {
    color: #000;
  }
  &:active 
  {
    color: #000;
  }
}

.back-button 
{
  text-align: left;
  font-size: 12px;
  padding: 0px 0px 0px 0px;
}

/**********************************
MIX-INs
***********************************/

@mixin text-decoration-style($value)
{
  @include prefixer(text-decoration-style, $value, moz webkit);
}

/**********************************
ANIMATIONS
***********************************/

@-webkit-keyframes {
  from {
    background-position:0
  }
  to {
    background-position:100%
  }
}

@-moz-keyframes play-fw {
  from {
    background-position:0
  }
  to {
    background-position:100%
  }
}

@-ms-keyframes play-fw {
  from {
    background-position:0
  }
  to {
    background-position:100%
  }
}

@-o-keyframes play-fw {
  from {
    background-position:0
  }
  to {
    background-position:100%
  }
}

@keyframes play-fw {
  from {
    background-position:0
  }
  to {
    background-position:100%
  }
}

@-webkit-keyframes play-bw {
  from {
    background-position:100%
  }
  to {
    background-position:0
  }

}
@-moz-keyframes play-bw {
  from {
    background-position:100%
  }
  to {
    background-position:0
  }
}

@-ms-keyframes play-bw {
  from {
    background-position:100%
  }
  to {
    background-position:0
  }
}

@-o-keyframes play-bw {
  from {
    background-position:100%
  }
  to {
    background-position:0
  }
}

@keyframes play-bw {
  from {
    background-position:100%
  }
  to {
    background-position:0
  }
}
/**********************************
HEADER
***********************************/

#siteheader 
{
  width: 100%;
  background-color: #fff;
  margin-right: auto;
  margin-left: auto;
  //transition: color .2s,background-color .2s;
} // end #siteheader 

#top-bar
{
position: fixed;
z-index: 999;
width: 100%;
background-color: #fff;
box-sizing: border-box;
top: 0px;
  
  .container 
  {
  margin-right: auto;
  margin-left: auto;
  box-sizing: border-box;

    .row
    {
    box-sizing: border-box;
    margin-right:0;
    margin-left:0;
  
      .topRow-4 
      {
      position: relative;
      min-height: 1px;
      padding: 5px 15px 0 15px;
      

        .dropnav   
        { 
        position: absolute;
        top: 10px;
        left: 15px;
        float:left;
        width: 24px;
        height: 24px;
        padding: 10px;
        cursor: pointer;

          .dropnavIcon 
          {
            display: inline;
            width: 24px;
            height: 24px;
            position: absolute;
            text-align: center;
            background-size: auto 100%;
            margin: auto;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            background-image: url(/images/burger_blk.png);
          } // end .dropnavIcon
        } // end .dropnav

        .dropnavOpen 
        { 
        position: absolute;
        top: 10px;
        left: 15px;
        float:left;
        width: 24px;
        height: 24px;
        padding: 10px;
        cursor: pointer;

          .dropnavIcon 
          {
            display: inline;
            width: 24px;
            height: 24px;
            position: absolute;
            text-align: center;
            background-size: auto 100%;
            margin: auto;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            background-image: url(/images/burger_blk.png);
            -webkit-animation:play-fw .25s steps(7) forwards;
            -moz-animation:play-fw .25s steps(7) forwards;
            -ms-animation:play-fw .25s steps(7) forwards;
            -o-animation:play-fw .25s steps(7) forwards;
            animation:play-fw .25s steps(7) forwards;
          } // end .dropnavIcon
        } // end .dropnavOpen

        .dropnavClosed 
        {
        position: absolute;
        top: 10px;
        left: 15px;
        float:left;
        width: 24px;
        height: 24px;
        padding: 10px;
        cursor: pointer;

          .dropnavIcon 
          {
            display: inline;
            width: 24px;
            height: 24px;
            position: absolute;
            text-align: center;
            background-size: auto 100%;
            margin: auto;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            background-image: url(/images/burger_blk.png);
            -webkit-animation:play-bw .25s steps(7) forwards;
            -moz-animation:play-bw .25s steps(7) forwards;
            -ms-animation:play-bw .25s steps(7) forwards;
            -o-animation:play-bw .25s steps(7) forwards;
            animation:play-bw .25s steps(7) forwards;
          } // end .dropnavIcon
        } // end .dropnavClosed


        .headerLogo 
        {
          display:block;
          margin: 0 auto;
          display:table;

          #logo 
          {
            box-sizing: border-box;
            margin: 0 auto;
            //display: block;

            a
            {
              margin: 0 auto;
              display: block;
            } // end a

            .h2Logo 
            {
              padding: 15px 0 0 0;
            } // end .h2Logo
            
            h2 
            {       
              font-size: 1.8em;
              line-height: .6em;
              padding: 19px 0 0 0;
              -webkit-margin-before:0em;
              -webkit-margin-after:0em;
              -webkit-margin-start: 0;
              -webkit-margin-end: 0;
              font-weight: bold;
            } // end h2

          } // end #logo

        } // end .headerLogo
        
        .menu1-1 
        {
          display:none;
          padding: 30px 0 0 30px;
          float:left;
        } // end .menu1-1

         .menu1-2 
        {
          display:none;
          padding: 30px 0 0 60px;
          float:left;
        } // end .menu1-1
      } //end .topRow-4
      
      .topRow-8
      {
      position:relative;
      min-height: 1px;
      padding: 0 15px;

        #dropnavMenu 
        {
        display:none;
        box-sizing: border-box;
        //margin: 0 auto;
        margin: 10px auto 0 auto;
         //z-index:1;

          ul 
          {
            display: block;
            width:100%;
            //overflow: hidden;
            padding: 1em 0 0 0;
            text-align: center;
            position:relative;
            list-style-type: none;
            color: #fff;
            -webkit-margin-before: 0;
            -webkit-padding-start: 0;
            -webkit-margin-after: 0;

            li.mobile
            {
              padding: 0;
              width: 100%;
              display: block;
              font-size: 1.3em;
              font-weight: bold;
              margin: .5em auto 0 auto;
              color: #000;
              text-align: center;
              //transition: all .3s ease-in-out;
              border:0;
              &:hover 
              {
              } // end &:hover
            } // end li.mobile

            li::after 
            {
            content: "";
            display:block;
            width: 100px;
            margin: .3em auto 0 auto;
            text-align: center;
            height:1px;
            background:#fff;
            border-bottom:2px solid #000;
            } // end li::after

            a 
            {
            text-decoration: none;
            color: #000;
              
              &:hover 
              {
              color: #767676;
              text-decoration: none;
              } // end &:hover
            } //end a

          } //end ul

        } //end #dropnavMenu 

      } //end .topRow-8

    }  // end .row

  } // end .container 

} // end #top-bar

/******************
MAIN CONTENT
******************/

#mainColumn 
{
  margin: 0 auto;
  padding: 6em 2.5% 0 2.5%;
  width: auto;

  .previewItem 
  {
  width: 100%;
  display: block;
  float:left;
  position:relative;
  margin: 0;
  padding: 30px;
  -webkit-box-sizing: border-box;
  vertical-align: baseline;
  }

  .first 
  {
  padding-top: 0px;
  }
  
  .previewItem span 
  {
  vertical-align: baseline;
  float: left;
  bottom: 0px;
  font-size: 1em;
  }

.smallBorder 
  {
  border-color: #8d8d8d;
  border-style: solid;
  border-width: 1px;
  }
} // end #mainColumn 

/******************
ABOUT
******************/

#contactColumn 
{
    margin: 30px auto 0 auto;
    padding: 6em 2.5% 0 2.5%;
    width: auto;

  .contactItem 
  {
    width: 95%;
    display: block;
    float:left;
    position:relative;
    margin: 0;
    padding: 0 2.5%;
    clear:left;

    p 
    {
      margin: 0;
      padding:0;
      line-height: 1.4em;
    } // end p

    span.blocky 
    {
      display: block;
      margin: 15px 0 0 0;
    } // end span.blocky 

    span.bold 
    {
    font-weight: bold;
    } // end span.bold
  }

  a 
  {
    text-decoration: none;
    border-bottom: 1px dotted;
  } // end a

  .primary 
  {
  display: block;
  float: left;
  } // end .primary

  .secondary 
  {
  clear: left;
  width: 100%;
  float: left;
  margin: 15px 0 0 0;
  clear:left;
  } // end .secondary
} // end #contactColumn 


/******************
FOOTER
******************/

footer 
{
  font-size: 1em;
  text-align: center;
  clear: both;
  padding: 30px 0 30px 0;
  color: #000;
  a 
  {
    font-weight: bold;
    text-decoration: none;
    padding: 15px 10px;
  }

} //end footer

//end of first main styles

