/**********************************
HEADER
***********************************/

  #siteheader 
  {
  width: 100%;
  background-color: #fff;
  margin-right: auto;
  margin-left: auto;
  } // end #siteheader 
  
  #top-bar 
  {
    position: fixed;
    z-index: 999;
    width: 100%;
    background-color: #fff;
    box-sizing: border-box;
    top: 0px;
    height: 65px !important;
  
    .container 
    {
    margin: 0 auto;
    box-sizing: border-box;
