          display: none;
          } // end .dropnavOpen

          .dropnavClosed 
          {
          display: none;
          } // end .dropnavClosed
          
          .headerLogo 
          {
          display:block;
          float:left;
          margin: 0 auto;
          position:absolute;
          top: 14px;
          left: 23px;

            #logo 
            {
              box-sizing: border-box;
              margin: 0 auto;
              //display: block;

              a
              {
                margin: 0 auto;
                display: block;
              } // end a

