        position:relative;
        height:80px;
        float:left;
        padding: 0 15px;
        width: 66.66666666666666%;
        box-sizing: border-box;
      
          #dropnavMenu 
          {
          display:block !important;
          box-sizing: border-box;
          //margin: 0 auto;
          margin: .9em 0 0 0;
          float: right;
        
            ul 
            {
                display:block;
                font-size:1.3em;
                margin: 0;
                //overflow: hidden;
                padding: 1em 0 0 0;
                text-align: center;
                list-style-type: none;
                color: #fff;
                -webkit-margin-before: 0;
                -webkit-padding-start: 0;
                -webkit-margin-after: 0;
                float:right;

              li.mobile
              {
                  //padding: 0;
                  //width: 100%;
                  display: inline;
                  font-size: 1.25em;
                  font-weight: bold;
                  padding: 0 2em 0 0;
                  color: #000;
                  //text-align: center;
                  //transition: all .3s ease-in-out;
                  border:0;
                &:hover 
                {
                }
              } // end li.mobile

              li::after 
              {
              display:none;
              }
            } // end ul

            a 
            {
                display:inline;
                text-decoration: none;
                color: #000;
                  
              &:hover 
              {
                  color: #767676;
                  text-decoration: none;
              }
            }  
          } // end #dropnavMenu 

        } // end .topRow-8

        } // end .row

    } // end .container

    .container::before
    {
    display: table;
    content: " ";
    } // end .container::before

    .container::after
    {
    clear: both;
    } // end .container::after

  } // end #top-bar


/******************
MAIN CONTENT
******************/

